<template>
  <div class="revise">
    <van-nav-bar
      title="修改密码"
      left-text="返回"
      right-text="完成"
      left-arrow
      @click-left="$toPage(-1)"
      @click-right="onClickFinish"
    >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <van-cell-group>
      <van-field
        v-model="password"
        type="password"
        label="旧密码"
        placeholder="请输入旧密码"
        required
        size="large"
      />
      <van-field
        v-model="passwordT"
        type="password"
        label="新密码"
        placeholder="请输入新密码"
        required
        size="large"
      />
      <van-field
        v-model="passwordS"
        type="password"
        label="确认密码"
        placeholder="请再次输入新密码"
        required
        size="large"
      />
    </van-cell-group>
  </div>
</template>
<script>
export default {
  data() {
    return {
      password: '',
      passwordT: '',
      passwordS: ''
    }
  },
  methods: {

    onClickFinish() {
      if (this.password.length <= 5 || this.password.length > 19) {
        this.$toast('请输入正确的密码, 要求6-18位大小写字母数字加字符组合!')
        return
      } else if (this.passwordT && this.passwordT === this.passwordS) {
        JNB._requestNative(
          JNB._transfer('userServer', 'LoginRegistrationModule', 'changeUserInfo'),
          JSON.stringify({
            oldPassword: this.password,
            newPassword: this.passwordT,
            oldPhone: '',
            newPhone: '',
            smsCode: ''
          }),
          (data) => {
            const dataStr = JSON.parse(data)

            if (dataStr.code === 200) {
              this.$toast(dataStr.message)
              this.$router.push({
                name: 'login'
              })
            } else {
              this.$toast(dataStr.message)
            }
          })
      } else {
        this.$toast('输入的两次新密码不一致!')
      }
    }
  }
}
</script>
<style scoped>
  .van-nav-bar__text{
    color: #323233;
  }
</style>

